
import { computed, defineComponent } from 'vue';
import { useStore } from '@/store';
import emitter from '@/bus';
export default defineComponent({
  props: {
    meInput: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  emits: ['write', 'moreBtn'],
  setup(props, { emit }) {
    emitter.emit('imgLoad');
    const write = () => {
      emit('write');
    };
    //品牌
    const store = useStore();
    const img = computed(() => {
      return props.meInput.imagePath.split('|');
    });
    const orderState = computed(() => {
      const state = props.meInput.auditStatus;
      let states = '';
      if (state === 0) {
        states = '待审核';
      } else if (state === 1) {
        states = '已审核';
      } else if (state === 2) {
        states = '审核未通过';
      }
      return states;
    });
    const brandId: any = computed(() => {
      let brandName = '';
      store.state.classify.brandItem.forEach((item) => {
        if (item.id == props.meInput.brandId) {
          brandName = item.brandName;
        }
      });
      return brandName;
    });
    //更多按钮
    const moreBtn = (id: number) => {
      emit('moreBtn', id);
    };
    return {
      write,
      moreBtn,
      brandId,
      img,
      orderState
    };
  }
});
